import React from 'react';
import { Helmet } from 'react-helmet';
//aos animation
import Aos from 'aos';
import 'aos/dist/aos.css';
//section components
import LandingPage from '../SectionComponents/Home/LandingPage';
import Moto from '../SectionComponents/Home/Moto';
import Members from '../SectionComponents/Home/Members';
import Projects from '../SectionComponents/Home/Projects';
import Videos from '../SectionComponents/Home/Videos';
import Blogs from '../SectionComponents/Home/Blogs';
import Footer from '../UiComponents/CommonUiComponents/Footer';
import JoinUs from '../SectionComponents/Home/JoinUs';
//Main Componet
const Home = () => {
  Aos.init({ duration: 100 });
  return (
    <div className='homepage'>
      <Helmet>
        <title>Welcome to Ginnovation Club</title>
        <meta
          name='description'
          content='Ginnovation club of GIET University, Gunupur is a step taken where students get a platform to present their ideas and innovative thoughts. The club helps these budding ideas by giving them a boost to convert ideas into invention of something appreciable and significant'
        />
      </Helmet>
      <LandingPage />
      <Moto />
      <Members />
      <Projects />
      <Videos />
      <JoinUs />
      <Blogs />
      <Footer />
    </div>
  );
};
export default Home;
