import React from 'react';
import { Col, Row } from 'antd';
//assets import
import StudentSwiper from '../../UiComponents/HomePage/StudentsSwiper';
import blogImg from '../../../assets/proud-student.svg';
//-----------------------------------------------------------------------
const Members = () => {
  return (
    <section className='proud-students'>
      <Row>
        <Col md={{ span: 6, order: 2 }} className='title'>
          <h2>Our Proud Members</h2>
          <div className='animation'>
            <img
              src={blogImg}
              alt='ginnovation students'
              className='side-img'
            />
          </div>
        </Col>
        <Col md={{ span: 18, order: 1 }}>
          <StudentSwiper />
        </Col>
      </Row>
      <div className='left-circle-border'></div>
    </section>
  );
};
export default Members;
