import { Col, Row } from 'antd';
import React from 'react';
import image from '../../../assets/featured-video.svg';
import VideoCard from '../../UiComponents/HomePage/VideoCard';
//----------------------------------------------------------------
const Videos = () => {
  return (
    <section className='home-video-section'>
      <header>
        <h2>Featured Videos</h2>
        <p>
          Lorem Ipsum is simply dummy text of the printing and typesetting
          industry
        </p>
      </header>
      <div className='videos'>
        <Row gutter={12}>
          <Col md={8}>
            <Row gutter={12}>
              <Col md={24}>
                <VideoCard />
              </Col>
              <Col md={24}>
                <VideoCard />
              </Col>
            </Row>
          </Col>
          <Col md={8}>
            <div className='content-card'>
              <div>
                <figure>
                  <img src={image} alt='ginnovation featured videos' />
                </figure>
                <center>
                  <button>View More Videos</button>
                </center>
              </div>
            </div>
          </Col>
          <Col md={8}>
            <Row gutter={12}>
              <Col md={24}>
                <VideoCard />
              </Col>
              <Col md={24}>
                <VideoCard />
              </Col>
            </Row>
          </Col>
        </Row>
      </div>
    </section>
  );
};
export default Videos;
