import { Col, Row } from 'antd';
import React from 'react';
import { Link } from 'react-router-dom';
import img from '../../../assets/joinus.jpg';
import JoinUsAnimation from '../../AnimationComponents/JoinUsAnimation';
const JoinUs = () => {
  return (
    <section className='join-us'>
      <Row>
        <Col md={10}>
          <div className='animation'>
            <JoinUsAnimation />
          </div>
        </Col>
        <Col md={14}>
          <div className='content'>
            <div>
              <h3>Join Ginnovation Club</h3>
              <h5>Do more than others</h5>
              <p>
                Lorem ipsum, dolor sit amet consectetur adipisicing elit.
                Repellendus, ad? Itaque, tenetur vitae laboriosam a quaerat
                explicabo quos facilis inventore! Minus ipsam natus dicta ipsa
                quod eum culpa in dolore?
              </p>
              <Link to='/signup'>
                <button>Register Now</button>
              </Link>
            </div>
          </div>
        </Col>
      </Row>
    </section>
  );
};

export default JoinUs;
