import React from 'react';
import { Col, Row } from 'antd';
//assets import
import GrowAnimation from '../../AnimationComponents/GrowAnimation';
import FbIcon from '../../../assets/Icons/facebook-black.png';
import IgIcon from '../../../assets/Icons/instagram-black.png';
import YtIcon from '../../../assets/Icons/youtube-black.png';
import GhIcon from '../../../assets/Icons/github.png';
import { Link } from 'react-router-dom';
//--------------------------------------------------------------------------
const LandingPage = () => {
  return (
    <section className='home'>
      <Row className='home-row'>
        <Col md={12}>
          <header>
            <h1>Ginnovation</h1>
            <h4>Know More, Grow More</h4>
            <p>
              Lorem Ipsum is simply dummy text of the printing and typesetting
              industry. Lorem Ipsum has been the industry's standard dummy text
              ever since the 1500s, when an unknown printer took a galley of
              type and scrambled it to make a type specimen book. It has
              survived not only five centuries
            </p>
            <Link to='/signup'>
              <button className='btn1'>Join Now</button>
            </Link>
          </header>
        </Col>
        <Col md={12}>
          <div className='animation-container'>
            <GrowAnimation
              src='../assets/Growth Animation.json'
              className='animation'
            />
          </div>
        </Col>
      </Row>
      <ul className='social-bar'>
        <li initial={{ x: 500 }} animate={{ x: 0 }} transition={{ delay: 0.4 }}>
          <a
            href='https://www.facebook.com/GInnovationClub/'
            target='_blank'
            rel='noreferrer'
          >
            <img src={FbIcon} alt='ginnovation facebook' />
          </a>
        </li>
        <li initial={{ x: 500 }} animate={{ x: 0 }} transition={{ delay: 0.5 }}>
          <a
            href='https://www.youtube.com/channel/UCgqysqyBDQJR2Badi2vwb3g'
            target='_blank'
            rel='noreferrer'
          >
            <img src={YtIcon} alt='ginnovation youtube' />
          </a>
        </li>
        <li initial={{ x: 500 }} animate={{ x: 0 }} transition={{ delay: 0.6 }}>
          <a
            href='https://www.instagram.com/ginnovationclub/'
            target='_blank'
            rel='noreferrer'
          >
            <img src={IgIcon} alt='ginnovation instagram' />
          </a>
        </li>
        <li initial={{ x: 500 }} animate={{ x: 0 }} transition={{ delay: 0.7 }}>
          <a
            href='https://github.com/GInnovation-Club'
            target='_blank'
            rel='noreferrer'
          >
            <img src={GhIcon} alt='ginnovation github' />
          </a>
        </li>
      </ul>
      <div
        initial={{ scale: 0.5, opacity: 0 }}
        animate={{ scale: 1, opacity: 1 }}
        transition={{ duration: 0.5 }}
        className='circle-design'
      ></div>
      <div
        initial={{ scale: 0.5, opacity: 0 }}
        animate={{ scale: 1, opacity: 1 }}
        transition={{ duration: 0.8 }}
        className='left-circle-border'
      ></div>
    </section>
  );
};
export default LandingPage;
